<template>
  <div>
    <tool-bar></tool-bar>
    <div id="wrap">
      <div id="wrap2">
        <div style="float:left; width:300px;">
          <div id="category">
            <!-- <input type="datetime-local" id="meeting-time"
       name="meeting-time" value="2018-06-12T19:30"
       min="2018-06-07T00:00" max="2018-06-14T00:00"> -->

            <p>  .</p>
            <select v-model="category">
              <option value="" selected="selected" disabled hidden></option>
              <option value="KOR"></option>
              <option value="CHI"></option>
              <option value="JAP"></option>
              <option value="PIZ"></option>
              <option value="FRI"></option>
              <option value="BUN"></option>
              <option value="FAS"></option>
              <option value="JOK">/</option>
              <option value="DES"></option>
              <option value="ETC"></option>
            </select>
          </div>
          
          <div id="postTTL">
            <p>   .</p>
            <input v-model="valid_time" type="datetime-local" 
            v-bind:min=minDate v-on:focusout="setMinValue" >
          </div>

          <div id="peopleNum">
            <p>  .</p>
            <select v-model.number="max_person_num">
              <option value="2" selected="selected">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="6">6</option>
              <option value="7">7</option>
            </select>
          </div>

          <div id="place">
            <label>   .</label>
            <div class="kmap" ref="map"></div>
            <input v-model="take_loc" v-if="inputVisible" 
            placeholder=" : ex)  ">
          </div>
        </div>

        <div style="float:right; width:350px;">
          <input v-model="post_name" id="post_name" placeholder=" .">
          <hr>
          <textarea v-model="post_content" id="post_content" type="text" placeholder=",   !"></textarea>
          <button type="submit" v-on:click.prevent="register"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ToolBar from '../components/ToolBar.vue'
import dayjs from 'dayjs'

export default {
  components:{
        ToolBar,
  },
  created(){
    this.$store.dispatch('FETCH_USER');
  },
  mounted() {
    let $vm = this;
    //    ()
    $vm.minDate = dayjs().format("YYYY-MM-DDTHH:mm");

    //   
    let kakao = window.kakao;
    let container = this.$refs.map;
    let options = { 
      center: new kakao.maps.LatLng(37.56676113296615, 126.97865227682179),
      level: 10
    };
    const mapInstance = new kakao.maps.Map(container, options); //    

    let geocoder = new kakao.maps.services.Geocoder();

    //  
    let marker = new kakao.maps.Marker({ 
      position: mapInstance.getCenter(),
    }); 
    marker.setMap(mapInstance);

    //  
    let zoomControl = new kakao.maps.ZoomControl();
    mapInstance.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);

    //   
    kakao.maps.event.addListener(mapInstance, 'click', function(mouseEvent) {        
      let latlng = mouseEvent.latLng; 

      marker.setPosition(latlng);

      $vm.latitude = latlng.getLat();   //   
      $vm.longtitude = latlng.getLng(); //   

      //  - 79  geocoder , 97  103    !
      let callback = function(result, status) {
        if (status === kakao.maps.services.Status.OK) {
            let depth1 = result[0].address.region_1depth_name;
            let depth2 = result[0].address.region_2depth_name;
            let depth3 = result[0].address.region_3depth_name;
            $vm.area_name = depth1+" "+depth2+" "+depth3;
        }
      }
      geocoder.coord2Address(latlng.getLng(), latlng.getLat(), callback);
      $vm.inputVisible = true;
    });
  },
  data() {
    return {
      post_name: "",        //  
      post_content: "",     //  
      category: "",  //  
      take_loc: "",         //    (ex. )
      participant_num: 0,   //   
      max_person_num: 2,    //  
      valid_time: "",       //   
      view_num:0,           // 
      area_name: "",        // ( )
      latitude: 0,          //   y()
      longtitude: 0,        //   x()
      // ------- member entity   --------------
      user_name: "",     //  
      
      minDate: "",
      inputVisible: false,
    }
  },
  methods: {
    register() {
      if (this.submitData()){
        // this.$store.dispatch('FETCH_USER') //  ->   
          const post = {
            post_name: this.post_name,           //  
            post_content: this.post_content,     //  
            category: this.category,             //  
            take_loc: this.take_loc,             //   
            // post_area : this.post_area,
            post_area : this.area_name,
            participant_num: 1,                  //   
            max_person_num: this.max_person_num, //  
            valid_time: this.valid_time,         //   
            view_num: this.view_num ,            // 
            nick:this.$store.state.member.nick,
            lat: this.latitude,
            lon: this.longtitude,
        }
        this.$store.dispatch('POST_DELIVERY_POST', post)
			}
      else {
        alert("  .")
      }
    },
		submitData() {
      if (this.post_name != "" && this.post_content != "" && 
          this.category != "" && this.take_loc != "" &&
          this.valid_time != "" ){
        return true;
      }
      else {
        return false;
      }
    },
    setMinValue() {
      const valid = dayjs(this.valid_time);
      if(valid.isBefore(dayjs(),"minute")) {
          alert('      .');
          this.valid_time = dayjs();
      }
    }
  }
}
</script>

<style scoped>
#wrap {
  background-color: #f2f2f2;
  height: 800px;
  padding: 80px 0;
}
#wrap2 {
    width: 800px;
    height: 570px;
    padding: 40px 50px 30px;
    margin: 0px auto;
    border-radius: 30px;
    background-color: white;
    text-align: left;
    font-size: 15px;
}
p{
  margin-top: 0px;
  margin-left: 3px;
}
select, #postTTL > input, #place > input{
    outline: none;
    width: 80%;
    height: 35px;
    background: #F8F8F8;
    border: 1px solid #BDBDBD;
    box-sizing: border-box;
    border-radius: 30px;
    padding: 8px 15px 9px;
    margin-bottom: 15px;
}
#postTTL > input {
  padding-left: 20px;
}
#post_name{
  font-weight: bold;
  font-size: 25px;
  color: #555;
  border: none;
  outline: none;
  margin-top: 5px;
  padding-left: 5px;
}
hr{
  margin: 25px 0;
}
#post_content{
  font-size: 15px;
  height: 400px;
  width: 300px;
  padding: 5px;
  border: none;
  outline: none;
}
button {
  float: right;
  width: 80px;
  height: 35px;
  border: none;
  border-radius: 10px;
  background-color: #ddd;
  margin-top: 20px;
  cursor: pointer;
}
.kmap{
  margin-top: 20px;
  width: 400px;
  height: 220px;
}
#place > input {
  margin: 15px 60px 0;
  text-align: center;
}
</style>