<template>
  <div>
    <tool-bar></tool-bar>
    <div id="wrap">
      <div id="wrap2">
        <div style="float:left; width:300px;">
          <div id="category">
            <p>  .</p>
            <select v-model="category">
              <option value="" selected="selected" disabled hidden></option>
              <option value="KOR"></option>
              <option value="CHI"></option>
              <option value="JAP"></option>
              <option value="PIZ"></option>
              <option value="FRI"></option>
              <option value="BUN"></option>
              <option value="FAS"></option>
              <option value="JOK">/</option>
              <option value="DES"></option>
              <option value="ETC"></option>
            </select>
          </div>
          
          <div id="postTTL">
            <p>   .</p>
            <input v-model="valid_time" type="datetime-local" 
            v-bind:min=minDate v-on:focusout="setMinValue">
          </div>
          <div id="peopleNum">
            <p>  .</p>
            <select v-model.number="max_person_num">
              <option value="2" selected="selected">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              <option value="6">6</option>
              <option value="7">7</option>
            </select>
          </div>
          <div id="place">
            <label>   .</label>
            <div class="kmap" ref="map"></div>
            <input v-model="take_loc" v-if="inputVisible" 
            placeholder=" : ex)  ">
          </div>
        </div>
        <div style="float:right; width:350px;">
          <input v-model="post_name" id="post_name" placeholder=" .">
          <hr>
          <textarea v-model="post_content" id="post_content" type="text" placeholder=",   !"></textarea>
          <button type="submit" v-on:click.prevent="register"></button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ToolBar from '../components/ToolBar.vue'
import Swal from 'sweetalert2';
import dayjs from 'dayjs'

export default {
  components:{
        ToolBar,
  },
  data() {
    return {
      post_id: this.$store.state.deliveryPost.post_id,
      post_name: this.$store.state.deliveryPost.post_name,     //  
      post_content: this.$store.state.deliveryPost.post_content,  //  
      category: this.$store.state.deliveryPost.category,      //  
      take_loc: this.$store.state.deliveryPost.take_loc,      //   
      participant_num: this.$store.state.deliveryPost.participant_num,  //   
      max_person_num: this.$store.state.deliveryPost.max_person_num,   //  
      valid_time: this.$store.state.deliveryPost.valid_time,       //   
      view_num: this.$store.state.deliveryPost.view_num,         // 
      user_name: this.$store.state.deliveryPost.member.nick,     //  
      area_name: this.$store.state.deliveryPost.post_area,     // 
      minDate: "",
      valueDate: "",
      latitude: this.$store.state.deliveryPost.lat,
      longtitude: this.$store.state.deliveryPost.lon,
      inputVisible: true,
    }
  },
  beforeCreate(){
    this.$store.dispatch('FETCH_USER');
    //URL      
    let post_id = this.$route.params.post_id;
    this.$store.dispatch('FETCH_DELIVERY_DETAIL', post_id);
    setTimeout(() => {
      let post = this.$store.state.deliveryPost
      this.post_id = post.post_id;
      this.post_name = post.post_name;     //  
      this.post_content = post.post_content;  //  
      this.category = post.category;      //  
      this.take_loc = post.take_loc;      //   
      this.participant_num = post.participant_num;  //   
      this.max_person_num = post.max_person_num;   //  

      const tmp_date1 = new Date(post.valid_time);
      const tmp_date2 = new Date(tmp_date1.getTime() - (tmp_date1.getTimezoneOffset() * 60000));
      const tmp_date3 = tmp_date2.toISOString();
      this.valid_time = tmp_date3.split('.')[0];

      this.view_num = post.view_num;         // 
      this.user_name = post.member.nick;     //  
      this.area_name = post.post_area;     // 
      this.latitude = post.lat;
      this.ongtitude = post.lon;
      if(this.$store.state.member.nick !== post.member.nick){
        alert("     .");
        this.$router.replace(`/delivery/post/${this.post_id}`);
      }
    }, 800);
  },
  mounted() {
    let $vm = this;
    //    ()
    $vm.minDate = dayjs().format("YYYY-MM-DDTHH:mm");
    //   
    let kakao = window.kakao;
    let container = this.$refs.map;
    let options = {
      center: new kakao.maps.LatLng(this.latitude, this.longtitude),
      level: 3
    };
    let mapInstance = new kakao.maps.Map(container, options); //    
    let geocoder = new kakao.maps.services.Geocoder();
    //  
    let marker = new kakao.maps.Marker({
      position: mapInstance.getCenter(),
    }); 
    marker.setMap(mapInstance);
    //  
    let zoomControl = new kakao.maps.ZoomControl();
    mapInstance.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
    //   
    kakao.maps.event.addListener(mapInstance, 'click', function(mouseEvent) {        
      let latlng = mouseEvent.latLng;

      marker.setPosition(latlng);

      $vm.latitude = latlng.getLat();  //   
      $vm.longtitude = latlng.getLng(); //   

      let callback = function(result, status) {
        if (status === kakao.maps.services.Status.OK) {
          let depth1 = result[0].address.region_1depth_name;
          let depth2 = result[0].address.region_2depth_name;
          let depth3 = result[0].address.region_3depth_name;
          $vm.area_name = depth1+" "+depth2+" "+depth3;
        }
      }
      geocoder.coord2Address(latlng.getLng(), latlng.getLat(), callback);
      $vm.inputVisible = true;
    });
  },
  methods: {
    register() {
      if (this.submitData()){
        // this.$store.dispatch('FETCH_USER') // 
          const post = {
            post_id: this.post_id,
            post_name: this.post_name,           //  
            post_content: this.post_content,     //  
            category: this.category,             //  
            take_loc: this.take_loc,             //   
            participant_num: this.participant_num,                  //   
            max_person_num: this.max_person_num, //  
            valid_time: this.valid_time,         //   
            view_num: this.view_num ,            // 
            nick: this.user_name,
            post_area :this.area_name,
            lat: this.latitude,
            lon: this.longtitude,
        }
        this.$store.dispatch('AMEND_DELIVERY_POST', post);
				Swal.fire({
          icon: 'success',
          title: ' .',
          confirmButtonText: '   ',
        }).then(() => {
          this.$router.replace(`/delivery/post/${this.post_id}`);
        })
			}
      else {
        alert("  .")
      }
    },
		submitData() {
      if (this.post_name != "" && this.post_content != "" &&  this.category != "" && this.take_loc != "" && this.valid_time != "") {
        return true;
      }
      else {
        return false;
      }
    },
    setMinValue() {
      const valid = dayjs(this.valid_time);
      if(valid.isBefore(dayjs(),"minute")) {
          alert('      .');
          this.valid_time = dayjs();
      }
    }
  }
}
</script>
<style scoped>
#wrap {
  height: 800px;
  padding: 80px 0;
  background-color: #f2f2f2;
}
#wrap2 {
  width: 800px;
  height: 570px;
  margin: 0px auto;
  padding: 40px 50px 30px;
  background-color: white;
  border-radius: 30px;
  font-size: 15px;
  text-align: left;
}
p {
  margin-top: 0px;
  margin-left: 3px;
}
select, #postTTL > input, #place > input{
  box-sizing: border-box;
  width: 80%;
  height: 35px;
  margin-bottom: 15px;
  padding: 8px 15px 9px;
  background: #F8F8F8;
  border: 1px solid #BDBDBD;
  border-radius: 30px;
  outline: none;
}
#postTTL > input {
  padding-left: 20px;
}
#post_name{
  margin-top: 5px;
  padding-left: 5px;
  border: none;
  color: #555;
  font-size: 25px;
  font-weight: bold;
  outline: none;
}
hr{
  margin: 25px 0;
}
#post_content{
  width: 300px;
  height: 400px;
  padding: 5px;
  border: none;
  font-size: 15px;
  outline: none;
}
button {
  width: 80px;
  height: 35px;
  margin-top: 20px;
  background-color: #ddd;
  border: none;
  border-radius: 10px;
  float: right;
  cursor: pointer;
}
.kmap{
  width: 400px;
  height: 220px;
  margin-top: 20px;
}
#place > input {
  margin: 15px 60px 0;
  text-align: center;
}
</style>