<!--        
    1. :class="{fixedWrapper:isShowmap, scrollWrapper:!isShowmap}"
    2.   ref="showMap" v-on:change="change()"  
    3.     
        <button v-on:click="this.$refs.showMap.showMap(), change()"></button>
    4. isShowmap : false change()  
    5. .fixedWrapper .scrollWrapper   -->
<template>
    <div v-show="Loading" class="loadingBox">
        <img src="../assets/loading.gif" alt="">
    </div>

    <div v-show="!Loading" :class="{visibleBox:openMap, invisibleBox:!openMap}">
      <div class="white-bg">
        <h4>  </h4>
        <div id="map" ref="map"></div>
        <p>{{this.detail_loc}}</p>
        <!-- <p>{{  }}</p> -->
        <button v-on:click="closeMap" class="close"></button>
      </div>
    </div>
</template>

<script>

export default {
    data(){
      return{
        isLoading: this.$store.getters.GET_ISLOADING,
        openMap: false,
        take_loc: this.$store.getters.GET_DELIVERY_POST.take_loc,      //   
        detail_loc: "",
        latitude: this.$store.getters.GET_DELIVERY_POST.lat,
        longtitude: this.$store.getters.GET_DELIVERY_POST.lon
      }
    },
    computed:{
      Loading(){
        return this.updateLoading();
      }
    },
    watch:{
      //isLoading     
      isLoading(flag){
        if(!flag){
          this.showMap();
        }
      }
    },
    mounted(){
        // console.log("show map mounted");
    },
    methods:{
      updateLoading(){
        this.isLoading = this.$store.getters.GET_ISLOADING;
        return this.isLoading;
      },
      showMap(){
        // console.log(" ");
        this.take_loc = this.$store.getters.GET_DELIVERY_POST.take_loc;     //   
        this.latitude = this.$store.getters.GET_DELIVERY_POST.lat;
        this.longtitude = this.$store.getters.GET_DELIVERY_POST.lon;

        let $vm = this;
        let kakao = window.kakao;
        let mapContainer = this.$refs.map;
        
        console.log($vm.latitude, $vm.longtitude);
        let coord = new kakao.maps.LatLng($vm.latitude, $vm.longtitude);  // ,  

        let mapOption = { 
            center: coord,
            level: 3, //   
            mapTypeId : kakao.maps.MapTypeId.ROADMAP
        };

        let map = new kakao.maps.Map(mapContainer, mapOption);

        let marker = new kakao.maps.Marker({
            position: coord
        });
        
        //     
        marker.setMap(map); 

        //  ->    
        let geocoder = new kakao.maps.services.Geocoder();

        let callback = function(result, status) {
          if (status === kakao.maps.services.Status.OK) {
            console.log(result[0]);
            $vm.detail_loc = result[0].address.address_name;
          }
        }
        geocoder.coord2Address(coord.getLng(), coord.getLat(), callback);


        //    HTML  document element 
        var iwContent = `<div id="infoWindow" style="padding:5px;">${this.take_loc}<br><a href="https://map.kakao.com/link/map/${this.take_loc},${$vm.latitude},${$vm.longtitude}" target="_blank"></a> <a href="https://map.kakao.com/link/to/${this.take_loc},${$vm.latitude},${$vm.longtitude}" target="_blank"></a></div>`; 
        var iwPosition = new kakao.maps.LatLng(33.450701, 126.570667); //  

        //     
        var infowindow = new kakao.maps.InfoWindow({
            position: iwPosition,
            content: iwContent
        });

        //    .   marker     
        infowindow.open(map, marker);


        this.openMap = true;
        // console.log(" ");
        return;
      },
      closeMap(){
        this.openMap = false;
      },
    }
}
</script>

<style>
#infoWindow a{
  color: white;
  padding: 0 3px;
  text-decoration: none;
  background-color: #669270;
  border-radius: 5px;
}
.wrapper{
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: hidden;
}
#map{
  width: 95%;
  height: 400px;
  margin: 0 auto;
  border-radius: 20px;
}
.visibleBox{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  
  width:100%;
  height:100%;
  background: rgba(0,0,0,0.6);
  visibility: visible;
  position: fixed;
  z-index: 1050;
}
.invisibleBox{
  width:100%;
  height:100%;
  visibility: hidden;
  position: fixed;
}
.invisibleBox h4{
  margin: 12px auto;
}
.visibleBox h4{
  margin: 12px auto;
}
.white-bg{
  width: 50%;
  margin: 170px auto;
  padding: 15px 0;
  background: white;
  border-radius: 62px;
}
.close{
  padding: 5px 15px;
  background:#ccc;
  border:none;
  border-radius: 10px;
  color:white;
  font-weight: bold;
  cursor:pointer;
}
.close:hover{
  transform: scale(1.1);
  transition: all 0.5s;
  color:white;
  font-weight: bold;
}
.loadingBox img{
  padding: 25% 50%;
  background-color: #ffffff;
  z-index: 10;
  width: 50px;
  position: absolute;
  left: 0px;
}
</style>